const router = createRouter({
  mode: "history",
  history: createWebHistory(process.env.NODE_ENV === "production"?'/':'/'),
  linkActiveClass: "active",
  routes,
});

router.beforeEach(async (to, from, next) => {
  let authenticated_routes = [
    "dashboard",
    "Users",
    "Brands",
    "Products",
    "AddProduct",
    "ViewProduct",
    "Categories",
    "AddCategory",
    "EditCategory",
    "AddBrand",
    "EditBrand",
    "DistributionPoint",
    "ViewDistributionpoint",
    "AddDistributionPoint",
    "Orders",
    "OrderProduct",
    "Section",
    "AddSection",
    "ViewSection",
    "StaticContent",
    "PrivacyPolicy",
    "AboutUs",
    "TermsAndConditions",
    "profile",
    "AdSense",
    "AddAdSense",
    "ViewAdSense",
    "Brochure",
    "AddBrochure",
    "ViewBrochure",
    "Content",
    "VestigeDashboard",
    "VestigeUsers",
    "VestigeProducts",
    "VestigeAddProduct",
    "VestigeViewProduct",
    "VestigeBrands",
    "VestigeAddBrand",
    "VestigeEditBrand",
    "hhiDashboard",
    "hhiUsers",
    "hhiProducts",
    "hhiAddProduct",
    "hhiViewProduct",
    "hhiBrands",
    "hhiAddBrand",
    "hhiEditBrand",
    "amulyaHerbalDashboard",
    "amulyaHerbalUsers",
    "amulyaHerbalProducts",
    "amulyaHerbalAddProduct",
    "amulyaHerbalViewProduct",
    "amulyaHerbalBrands",
    "amulyaHerbalAddBrand",
    "amulyaHerbalEditBrand",

    "proteinWorldDashboard",
    "proteinWorldUsers",
    "proteinWorldProducts",
    "proteinWorldAddProduct",
    "proteinWorldViewProduct",
    "proteinWorldBrands",
    "proteinWorldAddBrand",
    "proteinWorldEditBrand",
  ];
  let unchecked_routes = ["NotFound"];
  let user = localStorage.getItem("authentication_token")
  if(authenticated_routes.find(o=>o===to.name)){ 
    if(null == user){
      next({"name":"TeamLogin"});
    }else{
      next()
    }
  }else if(!unchecked_routes.find(o=>o===to.name)){ 
    if(user != null){
      localStorage.removeItem("authentication_token")
      next({"name":"TeamLogin"});
    }else{
      next()
    }
  }else{
    localStorage.removeItem("authentication_token")
    next({"name":"TeamLogin"});
  }

})

export default router;
